<template>
  <div class="invoice">
    <h2>
      <i onclick="window.history.go(-1)"></i>
      <span>发票信息</span>
      <em @click="invoice_sure">确认</em>
    </h2>
    <div class="invoice_content" >
      <p>
        <span>发票类型</span>
        <i><em :class="{active:!isDrowBill}" @click="invoice_choose_btn"></em>不开发票</i>
        <i><em :class="{active:isDrowBill}" @click="invoice_choose_btn"></em>电子发票</i>
      </p>
    </div>
    <div class="dianzi" v-if="isDrowBill">
      <p class="invoice_choose">
        <span>抬头类型</span>
        <i><em :class="{active:isPersonal}" @click="type_choose"></em>个人</i>
        <i><em :class="{active:!isPersonal}" @click="type_choose"></em>企业</i>
      </p>
      <div class="invoice_choose_result" >
        <p v-if="!isPersonal">
          <span>发票抬头</span>
          <input type="text" v-model="invoice_data.title" placeholder="请输入发票抬头">
        </p>
        <p v-if="!isPersonal">
          <span>纳税识别号</span>
          <input type="text" v-model="invoice_data.taxId" placeholder="请输入纳税识别号">
        </p>
        <p>
          <span>收票人邮箱</span>
          <input type="text" v-model="invoice_data.email" placeholder="请输入收票人邮箱">
        </p>
      </div>
    </div>
    

  </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
  name: 'invoice',
  data () {
    return {
      isDrowBill:false,
      isPersonal:true,
      invoice_data:{
        'type':1,
        'titleType':'',
        'title':'',
        'taxId':'',
        'email':''
      }
    }
  },
  computed:{
      invoice_info () {
        return this.$store.state.invoice_info
      }
  },
  created(){

    if(sessionStorage.invoice_info){
       this.$set(this,'invoice_data',this.invoice_info.title ? this.invoice_info : JSON.parse(sessionStorage.invoice_info));
       this.$nextTick(()=>{
        this.isDrowBill = this.invoice_data.type == 1 ? false : true;
        this.isPersonal = this.invoice_data.titleType == 1 ? true :false;
       })
    }
   
    // console.log(this.invoice_data)
  },
  methods:{
    invoice_sure(){
      if(!this.isDrowBill){
        this.invoice_data.type = 1 ;
        //若此时不开发票重置invoice_info
        this.$store.commit('newInvoice_info',this.invoice_data);
        window.localStorage.invoice_info = JSON.stringify(this.invoice_data);
      }else{
         this.invoice_data.type = 2 ;
         this.invoice_data.titleType = this.isPersonal?1:2;
         this.$store.commit('newInvoice_info',this.invoice_data);
        window.sessionStorage.invoice_info = JSON.stringify(this.invoice_data);
        if(!this.invoice_data.title && this.invoice_data.titleType == 2){
            let instance = Toast('请输入发票抬头');
            setTimeout(() => {
              instance.close();
            }, 2000);
            return false
        }else if(!this.invoice_data.taxId && this.invoice_data.titleType == 2){
            let instance = Toast('请输入发票税号');
            setTimeout(() => {
              instance.close();
            }, 2000);
            return false
        }else if(!this.invoice_data.email){
            let instance = Toast('请输入收票人邮箱');
            setTimeout(() => {
              instance.close();
            }, 2000);
            return false
        }
      }
      // console.log(this.invoice_data);
      this.$router.push({
        path:'./submit_order'
      })
    },
    invoice_choose_btn(){
      this.isDrowBill = !this.isDrowBill;
    },
    type_choose(){
      this.isPersonal = !this.isPersonal;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
@import '../common/css/var.scss';
.invoice{
  h2{
    height: 44px;
    line-height: 44px;
    display: flex;
    align-items:center;
    justify-content:space-between;
    padding:0 20px;
    font-family:PingFangSC-Regular;
    border-bottom: 1px solid #f4f4f4;/*no*/
    font-size:18px;
    i{
      width: 24px;
      height: 24px;
      background: url('../assets/order_navbar_arrow_back_icon@3x.png');
      background-size: 100%;
      margin-right: 10px;
    }
    span{
      flex:1;
      text-align: center;
      color:#212121;
    }
    em{
      color: $fontColor;
    }
  }
  .invoice_content{

    max-width: 414px;
    margin:0 auto;
    width: 100%;
    box-sizing:border-box;
    padding:0 20px;
    font-size: 14px;
    color: $fontColor;
    border-bottom: 10px solid $backgroundColor;
    p:nth-child(1){
      display: flex;
      justify-content:space-between;
      height: 44px;
      line-height: 44px;
      border-bottom: 1px solid #f4f4f4;/*no*/
      i{
        display: flex;
        align-items:center;
        em{
          width: 20px;
          height: 20px;
          margin-right: 5px;
          margin-top: -2px;
          background:url('../assets/payment_uncheck_common_icon@3x.png');
          background-size:100%; 
        }
        em.active{
          background: url('../assets/payment_check_common_icon@3x.png');
          background-size:100%; 
        }
      }
    }
    
  }
  
  .invoice_choose{
    max-width: 414px;
    margin:0 auto;
    display: flex;
    justify-content:space-between;
    align-items:center;
    height: 44px;
    line-height: 44px;
    padding: 0 20px;
    width: 100%;
    box-sizing:border-box;
    i{
      display: flex;
       align-items:center;
       em{
        width: 24px;
        height: 24px;
        margin-right: 5px;
        margin-top: -2px;
        background:url('../assets/order_setting_radio_off_common_icon@3x.png');
        background-size:100%; 
       }
       em.active{
        background: url('../assets/order_setting_radio_on_common_icon@3x.png');
        background-size:100%; 
       }
    }
  }
  .invoice_choose_result{
    max-width: 414px;
    margin:0 auto;
    p{
      font-size: 14px;
      display: flex;
      justify-content:space-between;
      align-items:center;
      height: 44px;
      line-height: 44px;
      padding: 0 20px;
      width: 100%;
      box-sizing:border-box;
      input{
        text-align: right; 
        padding: 5px 0; 
      }
    }
  }
}
</style>



